<!-- 福利 -->
<template>
  <div class="container ">
    <van-nav-bar fixed title="福利" @click-left="handleBack">
      <template #right>
        <img src="../assets/images/kf.png" @click="openLayer = true" />
      </template>
    </van-nav-bar>
    <div class=" main-content">
      <div class="welfare-list">
        <div class="act-item" v-for="(item, index) in datalist" :key="index" @click="viewIntroduce(item)">
          <img :src="item.image" />
          <div class="title">{{ item.title }}</div>
        </div>
      </div>
    </div>
  </div>
  <van-popup v-model:show="showPopup" round closeable position="bottom">
    <div class="activity-cont">
      <div class="title">{{ dataItem.title }}</div>
      <div class="act-img"><img :src="dataItem.image" /></div>
      <div class="scroll-cont">
        <div class="subtitle">活动说明：</div>
        <div class="cus-list" :class="dataItem.align">
          <div class="cus-item" v-for="(it, idx) in dataItem.child" :key="idx" v-html="it"></div>
        </div>
        <div class="subtitle">投注地址：</div>
        <div class="game-cont">
          <div class="game-item" v-for="item in gameList" :key="item.name">
            <label>{{ item.name }}:</label>
            <div class="address">{{ item.sortAddress }}</div>
            <van-icon name="qr" size="20" color="#4080ff" @click="handleQr(item)" />
            <div class="copy-btn" @click="copy(item)">复制</div>
          </div>
        </div>
        <div class="operate">
          <van-button block @click="handleReveice">领取奖励</van-button>
        </div>
      </div>
    </div>
  </van-popup>
  <div class="toast-box" v-if="gameItem">
    <div class="content">
      <span class="gukJud">投注地址复制成功！</span>
      <span class="gukJud">您投注的游戏为：【<span color="#FAFF00">{{ gameItem.name }}</span>】，投注限额为：</span>
      <span class="gukJud">{{ gameItem.trx }} USDT</span><span class="gukJud">{{ gameItem.usdt }} TRX</span>
    </div>
  </div>
  <van-popup v-model:show="showQrLayer" round>
    <div class="qr-layer">
      <div class="main-title">买【{{ qrData.name }}】投注地址二维码</div>
      <div class="qr-cont">
         <img :src="require('../assets/images/' + qrData.address + '.png')" />
        <p>扫描二维码转账投注</p>
      </div>
      <warn @open="handleOpen" />
    </div>
  </van-popup>
  <van-popup v-model:show="showLayer" closeable position="bottom">
    <register v-show="layerType === 'register'" @open="handleOpen" />
    <prohibit v-show="layerType === 'prohibit'" @open="handleOpen" />
  </van-popup>
  <!--  -->
  <van-popup round v-model:show="showLogin">
    <login @login="login" />
  </van-popup>
  <van-popup v-model:show="openLayer" closeable round>
    <service />
  </van-popup>
  <Footer :index="1" />
</template>

<script>
import useClipboard from "vue-clipboard3";
import Warn from "../components/Warn";
import Register from "../components/Register";
import Prohibit from "../components/Prohibit"
import { welfareList } from "../utils/resources"
import Footer from "../components/Footer";
import store from "../store";
import Login from "../components/Login";
import Service from "../components/Service";
export default {
  name: 'Welfare',
  components: { Warn, Register, Prohibit, Footer, Login, Service },
  data() {
    return {
      datalist: welfareList,
      dataItem: null,
      showPopup: false,
      gameList: [],
      gameItem: null,
      qrData: null,
      timer: null,
      showQrLayer: false,
      layerType: "",
      showLayer: false,
      showLogin: false,
      toClipboard: null,
      openLayer: false
    }
  },
  created() {
    const { toClipboard } = useClipboard();
    this.toClipboard = toClipboard;
    this.getGameList();
  },
  methods: {
    handleBack() {
      this.$router.back();
    },
    viewIntroduce(item) {
      this.dataItem = item;
      this.showPopup = true;
    },
    getGameList() {
      this.get("get_games").then(res => {
        let list = res.list || [];
        list.forEach(it => {
          let len = it.address.length;
          let start = (len - 10) / 2;
          it.sortAddress = it.address.substr(0, start) + '...' + it.address.substr(start + 10, len)
        })
        this.gameList = res.list;
      })
    },
    copy(item) {
      this.toClipboard(item.address);
      if (this.timer) clearTimeout(this.timer);
      this.gameItem = null;
      this.$nextTick(() => {
        this.gameItem = item;
      })
      this.timer = setTimeout(() => {
        this.gameItem = null;
      }, 1500)
    },
    //点击二维码
    handleQr(item) {
      this.qrData = item;
      this.showQrLayer = true;
    },
    handleOpen(type) {
      this.showQrLayer = false;
      this.layerType = type === 1 ? 'register' : 'prohibit'
      this.showLayer = true;
    },
    handleReveice() {
      let { token, wallet_address } = store.getters;
      if (!token || !wallet_address) return this.showLogin = true;
      this.$router.push("/benefits");
    },
    login(status) {
      this.showLogin = false;
      if (status) {
        this.$router.push("/benefits");
      }
    },
  }
}
</script>
<style lang='scss' scoped>
.container {
  height: 100%;
  overflow-y: auto;
  box-sizing: border-box;
  padding: 50px 10px;
  background-color: #f3f6fe;

  .welfare-list {
    padding-bottom: 20px;

    .act-item {
      font-size: 0;
      cursor: pointer;
      margin-top: 12px;
      border-radius: 8px;
      overflow: hidden;
      background-color: #fff;
      box-shadow: 0 2px 4px rgba($color: #000000, $alpha: 0.2);

      img {
        width: 100%;
      }

      .title {
        font-size: 14px;
        padding: 5px 10px;
        font-weight: bold;
      }
    }
  }
}

.activity-cont {
  padding: 10px;
  box-sizing: border-box;

  .title {
    color: var(--main-color);
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 10px;
  }

  .act-img {
    img {
      width: 100%;
    }
  }

  .scroll-cont {
    max-height: 400px;
    overflow-y: auto;

    .subtitle {
      color: var(--main-color);
      font-size: 14px;
      font-weight: bold;
    }

    .cus-list {
      padding-bottom: 10px;

      .cus-item {
        font-size: 12px;
        letter-spacing: 0.5px;
        margin-top: 10px;

        :deep(span) {
          color: #ff4444;
        }
      }

      &.center {
        text-align: center;
      }
    }

    .operate {
      .van-button {
        color: #fff;
        border-radius: 6px;
        background-color: #4080ff;
      }
    }
  }

  .game-cont {
    padding-top: 10px;

    .game-item {
      display: flex;
      -webkit-box-pack: justify;
      justify-content: space-between;
      -webkit-box-align: center;
      align-items: center;
      box-sizing: border-box;
      margin-bottom: 5px;
      height: 28px;
      width: 100%;
      border: 1px solid #ecf3ff;
      border-radius: 8px;

      label {
        margin-left: 4px;
        font-size: 12px;
        width: 65px;
      }

      .address {
        flex: 1 1 0%;
        color: var(--main-color);
        text-align: left;
      }

      .copy-btn {
        margin-left: 4px;
        color: var(--main-color);
        background-color: #ecf3ff;
        width: 39px;
        height: 100%;
        border-radius: 0px 8px 8px 0px;
        font-size: 12px;
        display: flex;
        -webkit-box-pack: center;
        justify-content: center;
        -webkit-box-align: center;
        align-items: center;
        box-sizing: border-box;
      }
    }
  }
}

.toast-box {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 90%;
  height: 120px;
  z-index: 3000;

  transform: translate(-50%, -50%);

  .content {
    display: flex;
    height: 100%;
    border-radius: 8px;
    align-items: center;
    color: #fff;
    font-size: 14px;
    justify-content: center;
    flex-direction: column;
    animation: fadeInUp 1s ease;
    background: rgba($color: #000000, $alpha: 0.8);

    .gukJud {
      padding: 2px 0;

      span {
        color: #faff00;
      }
    }
  }
}

.qr-layer {
  width: 300px;
  padding: 10px 0;
  text-align: center;

  .main-title {
    color: var(--main-color);
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 10px;
  }

  .qr-cont {
    >img {
      max-width: 200px;
    }

    p {
      padding-top: 10px;
      font-size: 13px;
    }
  }
}
</style>