<!-- 登录 -->
<template>
  <div class="login-cont">
    <div class="main-title">用户登录</div>
    <van-field v-model="address" center autocomplete="off" placeholder="请输入钱包地址">
    </van-field>
    <div class="btn-cont">
      <div class="btn" @click="handleClick(0)">取消</div>
      <div class="btn" @click="handleClick(1)">登录</div>
    </div>
  </div>
</template>

<script>
import store from '../store';
import { Login } from '../utils/public';
export default {
  name: '',
  data() {
    return {
      address: ''
    }
  },
  created() {
    let { wallet_address } = store.getters;
    if (wallet_address) this.address = wallet_address;
  },
  methods: {
    handleClick(status) {
      // store.commit("updateWalletAddress", this.address);
      if (status) {
        if (!this.address) return this.$toast("请输入钱包地址");
        this.$toast({ message: '正在登录中...', duration: 0, overlay: true })
        Login(this.address).then(() => {
          this.$toast("登录成功");
          this.$emit("login", status);
        }).catch(() => {
          this.$emit("login", false);
        })
      } else {
        this.$emit("login", status);
      }
    }
  }
}
</script>
<style lang='scss' scoped>
.login-cont {
  width: 300px;
  text-align: center;

  .main-title {
    color: var(--main-color);
    padding: 20px 0;
    font-size: 20px;
    font-weight: bold;
  }

  .btn-cont {
    display: flex;
    align-items: center;
    border-top: 1px solid #f1f1f1;

    .btn {
      flex: 1;
      cursor: pointer;
      color: #999;
      font-size: 16px;
      padding: 15px 0;

      &:last-child {
        color: var(--main-color);
        border-left: 1px solid #f1f1f1;
      }
    }
  }
}
</style>